* {
    margin:0;
    padding:0;
    box-sizing: border-box;
}
.villager{
    border-radius: 10px;
    background: white;
    margin-top: 1em;
    display: flex;
    flex-direction: column;
    align-items: center;
    height:45vh;
    width:45vw;
    max-width: 170px;
    max-height:250px;
    text-align: center;
    position: relative;
    box-shadow: 0px 3px 5px rgb(71,71,71);
}

.title{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.name{
    margin-right:8px;
}

h4{
    color:rgb(37, 33, 32)
}

.image{
    width:auto;
    height:75%;
    max-width: 100%;
    max-height: 70%;
    padding-bottom: 5%;
}


ul{
    display: flex;
    justify-content: space-around;
    width: 50%;
}

  li {
    list-style-type: none;
    text-align: center;
    font-weight: bold;
    text-shadow: -1px 0 rgb(180, 124, 40), 0 1px rgb(180, 124, 40), 1px 0 rgb(180, 124, 40), 0 -1px rgb(180, 124, 40);
  }

  .gender{
    text-shadow: -1px 0 rgb(180, 124, 40), 0 1px rgb(180, 124, 40), 1px 0 rgb(180, 124, 40), 0 -1px rgb(180, 124, 40);
  }
 

  .description{
    background: rgb(156, 98, 11);
    width: 100%;
    height: 25%;
    display: flex;
    flex-direction: column;
    padding-top: 2%;
    padding-bottom: 2%;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
  }


  .columns{
    float: left;
    position: relative;
    margin-right: 20px;
}

  .extra{
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      text-align: center;
      position: fixed;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%,-50%);
     
      width: 90%;
      max-width: 340px;
      height:80%;
      max-height: 500px;
      background: rgb(202, 152, 43);
      border: 2px solid black;
      z-index: 4;
      box-shadow: 0px 5px 20px 5px rgb(71,71,71);

  }


  .bigimg{
    width: 60%;
    height:60%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: white;
    border-right: 2px solid black ;
  }

  .bigimg > img{
    width:auto;
    height: auto;
    max-height: 90%;
    max-width: 90%;
    min-height: 62%;
}

.bigimg + div {
    width: 40%;
    height:30%;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    align-items: center;
}


.species{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}


.species> h4:first-child{
    margin-right: 4px;
}

.info {
    height: 40%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
  }


.info > div{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: calc(100% / 6);
    border-top: 2px solid black;
}

.info > div > div{
    height: 100%;
    display: flex;
    align-items: center;
    text-align: left;
    padding-left: 5px;
}

.clothes{
    height: 100%;
    display: flex;
    text-align: left;
    padding: 2.5% 5px 2.5%;
    width: 72%;
    font-size: 12px;
    overflow: scroll;
}

.info > div > div:first-child{
    border-right: 2px solid black;
    width: 28%;
    min-width: 90px;
}

.info > div > div:first-child + div {
    width: 72%;
    font-size: 12px;
}