.App {
  background-color: rgb(22, 163, 34);
  height:100%;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  position: relative;
  border: 1px solid rgb(231, 196, 40);
}

img{
  margin-top: 1em;
  max-width: 300px;
  height: auto;
}

.animals{
  width:100%;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
  margin-bottom: 5em;
}

.suggested{
  width:calc(40% + 2px);
  border: 2px solid rgb(231, 196, 40);
  border-top: none;
  position: absolute;
  background: none;
  min-width: calc((250px * 0.8) + 2px);
  background: rgb(153, 96, 11);
  z-index: 3;
}



.suggested li {
  list-style-type: none;
  text-align: left;
  padding-left: 5px;
  padding-top: 2px;
  padding-bottom: 2px;
  width:100%;
  color: rgb(231, 196, 40);
}

form {
  display: flex;
  flex-direction: row;
  margin-top: 1em;
  width: 50%;
  height:30px;
  min-width: 250px;
}

form > div {
  width:80%;
}

input{
  border: 2px solid rgb(231, 196, 40);
  padding: 5px;
  border-right: none;
  border-bottom: none;
  background: rgb(180, 124, 40);
  width:100%;
  height: calc(100% - 2px);
  font-size: 16px;
  outline: none;
}


::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
  color: rgb(243, 224, 161);
  opacity: 1; /* Firefox */
}

:-ms-input-placeholder { /* Internet Explorer 10-11 */
  color: rgb(243, 224, 161);
}

::-ms-input-placeholder { /* Microsoft Edge */
  color: rgb(243, 224, 161);
}

form button{
  background: rgb(153, 96, 11);
  color: rgb(231, 196, 40);
  font-size: 15px;
  font-weight: bold;
  border: 2px solid rgb(231, 196, 40);
  width:20%;
  height: 100%;
}

.alert{
  margin-top: 1em;
  color:rgb(231, 196, 40);
  font-weight: bold;
  font-size: 20px;
  text-shadow: -1px 0 rgb(180, 124, 40), 0 1px rgb(180, 124, 40), 1px 0 rgb(180, 124, 40), 0 -1px rgb(180, 124, 40);

}

.card{
  position: relative;
}

.remove{
  z-index: 2;
  background: rgb(228, 69, 69);
  width:30px;
  height: 30px;
  font-weight: bold;
  font-size: 20px;
  position: absolute;
  top:0;
  right: 0;
  display: flex;  
  justify-content: center;
  align-items: center;
  border: none;
  border-top-right-radius: 8px;
  border-bottom-left-radius: 8px;
  transform: translate(-0.5%,53.5%);
}


.end {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background:rgb(180, 124, 40);
  width: 100%;
  height: 10vh;
  position: absolute;
  bottom:0;
  font-size: 12px;
  border-top: 1px solid rgb(231, 196, 40);
}

a{
  margin-bottom:0.5em;
}